<template>
    <el-card  v-loading="loading">
    <slot-common slot='header' style="font-size:18px;">
    <!-- 面包屑的插槽 具名插槽  title-->
    <template slot='title'></template>
   </slot-common>
   <el-table
      :data="tableData"
      stripe
      style="width: 100%">
      <el-table-column
        prop="title"
        label="标题"
        width="400">
      </el-table-column>
      <el-table-column
        :formatter="formatter"
        prop="comment_status"
        label="评论状态"
       >
      </el-table-column>
      <el-table-column
        prop="total_comment_count"
        label="总评论数">
      </el-table-column>
      <el-table-column
        prop="fans_comment_count"
        label="粉丝评论数">
      </el-table-column>
      <el-table-column

        label="操作">
        <!-- 作用域插槽 父组件 写组件内容 调用子组件数据 -->
        <!-- obj相当于 el-table-column下子组件中给slot的属性集合 -->
        <template slot-scope="obj">
        <el-button type="text">修改评论</el-button>
        <el-button @click="closeOrOpen(obj.row)" :style="{color: obj.row.comment_status ? '#E6A23C' : '#409EFF'}" type="text">{{obj.row.comment_status ? '关闭评论':'打开评论'}}</el-button>
        </template>
      </el-table-column>
   </el-table>
   <!-- 分页管理 -->
    <el-row type="flex" justify="center" style="margin:20px 0">
        <el-pagination
        :page-size="page.pageSize"
        :total="page.total"
        :current-page="page.currentPage"
         @current-change="changePage"
         background
    layout="prev, pager, next"
    >
    </el-pagination>
    </el-row>
    </el-card>
</template>

<script>
export default {
  data () {
    return {
      tableData: [],
      page: {
        pageSize: 10,
        total: 0,
        currentPage: 1
      },
      loading: false
    }
  },
  methods: {
    changePage (newPage) {
      // 用最新页码区查询
      this.page.currentPage = newPage // 将当前最新页码赋值给data中的变量
      this.GetCommonts() // 获取当前newPage的数据
    },
    // 打开或者关闭
    closeOrOpen (row) {
      let mess = row.comment_status ? '关闭' : '打开'
      console.log(row.id)
      this.$confirm(`您确定要${mess}评论吗`, '提示').then(() => {
        this.$http({
          method: 'put',
          url: '/comments/status',
          params: { article_id: row.id.toString() },
          data: { allow_comment: !row.comment_status } // 状态是反着的
        }).then(() => {
          // 如果进入到then函数 一定成功
          this.GetCommonts()
        })
      })
    },
    // 查询列表数据
    GetCommonts () {
      this.loading = true // 将加载进度设置成加载状态
      let pageParams = { page: this.page.currentPage, // 将当前页数 和 页码赋值给一个对象
        per_page: this.page.pageSize } // 页码参数
      this.$http({
        url: '/articles',
        params: {
          response_type: 'comment', // 查询评论相关的数据
          ...pageParams // 将获取到的页码和当前页数赋值传递过去
        }
      }).then(result => {
        this.tableData = result.data.results // 取到列表数据 给 当前的数据对象
        console.log(result)
        this.page.total = result.data.total_count // 文章评论列表总数 赋值给当前分页的总数
        this.loading = false // 关闭加载进度
      })
    },
    formatter (row, column, cellValue, index) {
      return cellValue ? '正常' : '关闭'
    }
  },
  created () {
    this.GetCommonts()
  }
}
</script>

<style lang="less" scoped>
</style>
